import React, { Component } from 'react'
import {
  extendParentClass,
  extendBaseClass,
  extend,
} from '../../../utils/classes'
import { ReactComponent as Messaging } from './messaging.svg'
import { ReactComponent as PlayIcon } from '../../../icons/play-button.svg'

export class ToggleVideo extends Component {
  baseClass = extendParentClass.bind(this)('toggle-video')

  extend = extendBaseClass.bind(this)

  handleClick = () => {
    const { setShowVideo } = this.props
    setShowVideo(true)
  }

  render() {
    const playButton = this.extend('play')
    return (
      <button className={this.baseClass} onClick={this.handleClick}>
        <span className={playButton}>
          <PlayIcon className={extend(playButton, 'icon')} />
        </span>
        <Messaging className={this.extend('image')} />
      </button>
    )
  }
}

export default ToggleVideo
