import React, { Component } from 'react'
import { ReactComponent as ArrowRight } from '../../../icons/arrow-right.svg'
import { extendParentClass, extendBaseClass } from '../../../utils/classes'

export class Trial extends Component {
  baseClass = extendParentClass.bind(this)('action')

  extend = extendBaseClass.bind(this)

  render() {
    return (
      <button className={this.baseClass}>
        Get Free Trial <ArrowRight className={this.extend('icon')} />
      </button>
    )
  }
}

export default Trial
