import React, { Component } from 'react'
import { extendParentClass, extendBaseClass } from '../../../../utils/classes'

export class Joined extends Component {
  baseClass = extendParentClass.bind(this)('joined')

  extend = extendBaseClass.bind(this)

  render() {
    return (
      <div className={this.baseClass}>
        <span className={this.extend('title')}>People Already Joined</span>
        <span className={this.extend('amount')}>455,326,234</span>
      </div>
    )
  }
}

export default Joined
